<template>
  <div>
    <h3
      class="title is-3 column is-4-desktop is-offset-4 is-full-mobile"
    >Abaixo estão as solicitacões de serviços feitas para voçê!</h3>
    <br>
    <div v-if="this.pro.imageForUserId[0]">
      <ApolloQuery
        :query="require('@/graphql/queries/solicitacoes.gql')"
        :variables="{user_recebe_id:this.pro.imageForUserId[0].id}"
      >
        <template slot-scope="{ result: { data, loading },isLoading }">
          <div v-if="isLoading">Loading...</div>
          <div v-else>
            <div v-for="solicitacao of data.propostaRecebe" :key="solicitacao.id">
              <div class="column is-12-dektop is-full-mobile">
                <div class="card">
                  <div class="columns">
                    <div class="column is-3-desktop is-full-mobile">
                      <div class="column">
                        <div class="level-item">
                          <img
                            class="img"
                            width="80%"
                            height="10%"
                            :src="`http://graphql.me/perfilCli/${solicitacao.user_envia.imagem}`"
                            alt="Placeholder image"
                          >
                        </div>
                        <div class="title is-5 level-item">
                          <b>Solicitação de ></b>
                          <div class="level-item">{{solicitacao.user_envia.user.name}}</div>
                        </div>
                      </div>
                    </div>
                    <div class="column is-9-desktop is-full-mobile">
                      <div class="columns">
                        <div class="column is-6-desktop">
                          <div class="column">
                            <div class="title is-3">{{solicitacao.titulo}}</div>
                          </div>
                        </div>
                        <div class="column is-offset-2">
                          <div class="column">
                            <div v-if="solicitacao.status===0" class="espera">Esperando resposta</div>
                            <div v-if="solicitacao.status===1" class="aceitou">Pedido aceito :)</div>
                            <div v-if="solicitacao.status===2" class="recusou">Pedido recusado :(</div>
                            <div v-if="solicitacao.status===3" class="finalizou">Pedido Finalizado!</div>
                            <div v-if="solicitacao.status===4" class="finalizou">Você foi avaliado!</div>
                          </div>
                        </div>
                      </div>
                      <div class="column is-9-desktop">
                        <div class="title is-5">
                          <b>Local do serviço:</b>
                          {{solicitacao.local}}
                        </div>
                      </div>
                      <div class="columns">
                        <div class="column is-6-desktop">
                          <div class="column">
                            <div class="title is-5">
                              <b>Valor:</b>
                              {{solicitacao.valor}}
                            </div>
                          </div>
                        </div>
                        <div class="column">
                          <div class="column">
                            <div class="title is-5">
                              <b>Tipo do serviço:</b>
                              {{solicitacao.tipo}}
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="column is-9-desktop">
                        <div class="title is-5" style="text-align:justify">
                          <b>Descrição do serviço:</b>
                          {{solicitacao.descricao}}
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="column is-12-desktop is-full-mobile">
                    <div class="columns">
                      <div class="column is-7-desktop" v-if="solicitacao.status !==2">
                        <div class="column">
                          <div
                            class="title is-4 obsEspera"
                            style="text-align:justify"
                            v-if="solicitacao.status===0"
                          >OBS: Você pode aceitar ou recusar a proposta do(a) {{solicitacao.user_envia.user.name}} logo ao lado!</div>
                          <div
                            class="title is-4 obsAceita"
                            style="text-align:justify"
                            v-if="solicitacao.status===1"
                          >Você aceitou o pedido do(a) {{solicitacao.user_envia.user.name}}, entre em contato com ele(a) através do número: {{solicitacao.user_envia.telefone}}, email: {{solicitacao.user_envia.user.email}} ou encontre-o(a) pessoalmente pelo endereço {{solicitacao.user_envia.logradouro}} - {{solicitacao.user_envia.cidade}} - {{solicitacao.user_envia.estado}}</div>
                        </div>
                      </div>
                      <div
                        class="column is-12-desktop is-full-mobile"
                        v-if="solicitacao.status ===2"
                      >
                        <div class="column">
                          <div
                            class="title is-4 obsRecusa"
                            style="text-align:center"
                            v-if="solicitacao.status===2"
                          >Você recusou o pedido do(a) {{solicitacao.user_envia.user.name}}, ele(a) já foi avisado(a)</div>
                        </div>
                      </div>
                      <div class="column is-5-desktop is-full-mobile">
                        <div class="column">
                          <div class="columns" v-if="solicitacao.status === 0">
                            <button
                              class="btn-12 column is-full-mobile"
                              @click.prevent="aceitar(solicitacao.id)"
                            >Aceitar Proposta</button>
                            <button
                              class="btn-13 column is-full-mobile"
                              @click.prevent="recusar(solicitacao.id)"
                            >Recusar Proposta</button>
                          </div>
                          <div v-if="solicitacao.orcamento[0]">
                            <div class v-if="solicitacao.status === 1">
                              <div
                                class="title is-5 obs"
                                style="text-align:justify"
                                v-if="solicitacao.status===1"
                              >OBS: Só finalize o serviço quando acabar!</div>
                              <div class="column">
                                <button
                                  class="btn-15 column is-12-desktop is-full-mobile"
                                  @click.prevent="finalizar(solicitacao.id)"
                                >Finalizar Serviço</button>
                              </div>
                            </div>
                          </div>
                          <div v-if="!solicitacao.orcamento[0] && solicitacao.status === 1">
                            <div class="column is-12-desktop is-full-mobile">
                              <router-link
                                :to="`/page/profissional/solicitacoes/orcamento/${solicitacao.id}`"
                              >
                                <button
                                  class="btn-14 column is-12-desktop is-full-mobile"
                                >Mandar sua disponibilidade!</button>
                              </router-link>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div v-if="solicitacao.status === 3">
                      <div
                        class="title is-4 obsFinaliza column is-12-desktop is-full-mobile"
                        style="text-align:center"
                      >Esse pedido foi finalizado com sucesso!</div>
                    </div>
                    <div v-if="solicitacao.status ===4">
                      <div
                        class="title is-4 obsFinaliza column is-12-desktop is-full-mobile"
                        style="text-align:center"
                      >O(a) avaliou você nesse serviço, olhe seu perfil!</div>
                    </div>
                  </div>
                  <br>
                </div>
                <!-- FIM DO CARD -->
              </div>
              <!-- Fim da <div class="column is-12-dektop is-full-mobile"> -->
            </div>
            <!-- FIM DO V-FOR -->
          </div>
        </template>
      </ApolloQuery>
    </div>
    <div v-else>
      <div class="title is-4 level-item">Cadastre-se para receber solicitações de outros usuários</div>
    </div>
  </div>
</template>
<script>
import gql from "graphql-tag";
import proForUserId from "@/graphql/queries/imagemQuery.gql";
import aceita from "@/graphql/mutations/aceita.gql";

export default {
  data() {
    return {
      me: null,
      pro: []
    };
  },
  apollo: {
    me: gql`
      query {
        me {
          id
          name
          email
          status
        }
      }
    `,
    pro: {
      query: proForUserId,
      variables() {
        return {
          id: this.me.id
        };
      },
      update(data) {
        this.pro = data;
        return this.pro;
      }
    }
  },
  methods: {
    aceitar(idproposta) {
      this.$apollo
        .mutate({
          mutation: aceita,
          variables: {
            id: idproposta,
            status: 1
          }
        })
        .then(() => {
          this.$router.push(
            `/page/profissional/solicitacoes/orcamento/${idproposta}`
          );
        })
        .catch(error => {
          console.error(error);
        });
    },
    recusar(idproposta) {
      this.$apollo
        .mutate({
          mutation: aceita,
          variables: {
            id: idproposta,
            status: 2
          }
        })
        .then(() => {
          window.location.reload();
        })
        .catch(error => {
          console.error(error);
        });
    },
    finalizar(idproposta) {
      this.$apollo
        .mutate({
          mutation: aceita,
          variables: {
            id: idproposta,
            status: 3
          }
        })
        .then(() => {
          window.location.reload();
        })
        .catch(error => {
          console.error(error);
        });
    }
  }
};
</script>
<style>
@import "../../cssFiles/inputsAndButtons.css";

.img {
  border-radius: 100%;
}

.espera {
  background: yellow;
  text-transform: uppercase;
  padding: 0.2em 0.7em;
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
  border: 2px solid black;
}
.aceitou {
  background: seagreen;
  text-transform: uppercase;
  padding: 0.2em 0.7em;
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
  border: 2px solid black;
  color: white;
}

.recusou {
  background: red;
  text-transform: uppercase;
  padding: 0.2em 0.7em;
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
  border: 2px solid black;
  color: white;
}

.finalizou {
  background: rgb(99, 74, 110);
  text-transform: uppercase;
  padding: 0.2em 0.7em;
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
  border: 2px solid black;
  color: white;
}

.obsFinaliza {
  background: rgb(99, 74, 110);
  padding: 1em 2em;
  color: white;
}

.obsEspera {
  background: rgb(250, 250, 127);
  padding: 1em 2em;
}

.obsAceita {
  background: rgb(127, 219, 127);
  padding: 1em 2em;
}

.obsRecusa {
  background: rgb(226, 118, 118);
  padding: 1em 2em;
}

.obs {
  background: grey;
  padding: 1em 2em;
}
</style>